.user-info {
    margin-bottom: 44px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
}

.user-cover {
    -ms-flex: 1 1;
        flex: 1 1;
    margin-right: 30px;
}

.user-cover a {
    display: block;
    width: 152px;
    height: 152px;
    margin: 0 auto;
    border-radius: 50%;
}

.user-cover img {
    width: 100%;
    border-radius: 50%;
}

.user-content {
    -ms-flex: 2 1;
        flex: 2 1;
}

.user-content-profile {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    margin-bottom: 20px;
}

.user-content-profile-name {
    font-size: 32px;
    margin: 0;
    font-weight: 200;
}

.user-content-profile-edit {
    margin-left: 20px;
    padding: 4px 24px;
    border: 1px solid #eee;
    border-radius: 3px;
    color: black;
    text-decoration: none;
}

.user-content-profile-settings {
    color: black;
    margin: 2px 0 0 12px;
}

.user-content-follow {
    margin: 0 0 20px;
    padding: 0;
    list-style: none;
    display: -ms-flexbox;
    display: flex;
}

.user-content-follow li {
    margin-right: 20px;
}

.user-content-intro h3 {
    display: inline;
}

.user-content-intro span {
    margin-left: 10px;
}


